import { Component } from '@angular/core';
// import { TWADialogsModule } from 'twa-md2-dialogs';
// import { TWADialogsModule } from '../../projects/twa-md2-dialogs/src/public_api';
import { TWADialogsModule } from 'twa-md2-dialogs';

@Component({
  selector: 'ld-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ld';

  constructor(
    private dialogsService: TWADialogsModule
  ) {}

    testDialog() {
        this.dialogsService
            .prompt(
                'Iniciar bscula',
                'Introduzca los datos asociados al proceso de pesado:',
                [
                    {
                        key: 'cea',
                        label: 'CEA',
                        type: 'text',
                        value: '',
                        fxFlex: 'none',
                        autocomplete: {
                            forceSelect: true,
                            options: [
                                'test 1',
                                'test 2',
                                'test 3',
                                'test 4'
                            ]
                        }
                    },
                ],
                'Enviar',
                'Cancelar')
            .subscribe(res => {
                if (res !== false && typeof res.cea !== 'undefined') {
                    console.log(res);
                }
            }
        );
    }
}
